<template>
  <input
    v-model="model"
    class="dadata-input"
    @change="$emit('change')"
    type="text"
    autocomplete="off"
  >
</template>

<script>
  import $ from 'jquery';
  import '../dadata.js';

  export default {
    name: 'DadataSuggestions',
    pluginOptions: {},
    props: {
      value: {
        type: String,
        default: '',
      },
      /** Поле для хранения полной информации о результате */
      fullInfo: {},
      /** Параметры для Дадата */
      options: {},
      /** API-ключ */
      token: {
        type: String,
        default: '',
      },
      /** Тип подсказок */
      type: {
        type: String,
        default: '',
      },
      fieldValue: {
        type: String,
        default: 'value',
      },
    },
    data() {
      return {
        model: '',
        currentOptions: {
          /** API-ключ */
          token: '',
          /** Тип подсказок */
          type: '',

          /** ИДЕНТИФИКАТОР_РАЗРАБОТЧИКА */
          partner: ''
        },
        defaultOptions: {
          /** Прокручивать текстовое поле к верхней границе экрана при фокусе */
          scrollOnFocus: false,
          /** Автоматически подставлять подходящую подсказку из списка, когда текстовое поле теряет фокус.*/
          triggerSelectOnBlur: false,
          /** Автоматически подставлять подходящую подсказку из списка при нажатии на Enter. */
          triggerSelectOnEnter: true,
          /** Что показывать в правом углу текстового поля подсказок */
          addon: 'spinner',
        },
      };
    },
    created() {
      this.parseOptions()
      if (!this.currentOptions.token) {
        console.warn('Dadata: Необходимо указать API-токен');
      }

      if (!this.currentOptions.type) {
        console.warn('Dadata: Необходимо указать тип подсказок');
      }
    },

    mounted() {
      this.model = this.value;
      this.initSuggestion();
    },

    methods: {
      parseOptions() {
        this.currentOptions = {...this.currentOptions, ...this.defaultOptions};
        this.currentOptions = {...this.currentOptions, ...this.$options.pluginOptions};
        this.currentOptions = {...this.currentOptions, ...this.options};
        if (this.token) this.currentOptions.token = this.token;
        if (this.type) this.currentOptions.type = this.type;

        this.currentOptions = Object.assign(this.currentOptions, {
          onSelect: suggestion => {
            this.$emit('update:fullInfo', suggestion);
            this.$emit('change', suggestion);
            this.onSelect(suggestion);
          },
        });
        this.currentOptions = Object.assign(this.currentOptions, {
          onSearchError: (q, w, e, r, t) => {
            if (w.status === 403) {
              console.warn('Ошибка доступа! \n ' + w.responseJSON.message)
            }
          },
        });

        this.currentOptions.partner = 'VUE.76941';
      },
      /**
       * Инициализация модуля
       */
      initSuggestion() {
        $(this.$el).suggestions(this.currentOptions);
      },

      destroySuggestion() {
        const plugin = $(this.$el).suggestions();
        plugin.dispose();
      },

      /**
       * Выбор элемента из списка
       */
      onSelect(suggestion) {
        if (this.fieldValue === 'value' || this.fieldValue === 'unrestricted_value') {
          this.model = suggestion[this.fieldValue];
        } else {
          this.model = suggestion.data[this.fieldValue];
        }
      },
    },

    destroyed() {
      this.destroySuggestion();
    },

    watch: {
      model() {
        this.$emit('input', this.model);
      },
      value() {
        this.model = this.value;
      },
    },
  };
</script>

<style>
  .dadata-input {
    border-radius: 5px;
    padding: 10px 15px;
    border: 1px solid rgba(29, 29, 27, 0.3);
    transition: all 0.2s ease;
  }

  .suggestions-nowrap {
    white-space: nowrap
  }

  .suggestions-input {
    -ms-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 100%
  }

  .suggestions-input::-ms-clear {
    display: none
  }

  .suggestions-wrapper {
    position: relative;
    margin: 0;
    padding: 0;
    vertical-align: top;
    -webkit-text-size-adjust: 100%
  }

  .suggestions-addon {
    display: none;
    opacity: 0;
    position: absolute
  }

  .suggestions-addon[data-addon-type=spinner] {
    background: rgba(255, 255, 255, .75) url(data:image/gif;base64,) 50% 50% no-repeat;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%
  }

  .suggestions-addon[data-addon-type=clear] {
    background: transparent url(data:image/svg+xml;base64,PHN2ZyB3aWR0aD0nMTAwJyBoZWlnaHQ9JzEwMCcgeG1sbnM9J2h0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnJz48Zz48cGF0aCBkPSdtMjAsMjBtMTAsMGwyMCwyMGwyMCwtMjBsMTAsMTBsLTIwLDIwbDIwLDIwbC0xMCwxMGwtMjAsLTIwbC0yMCwyMGwtMTAsLTEwbDIwLC0yMGwtMjAsLTIwbDEwLC0xMCcgZmlsbD0nIzk5OScvPjwvZz48L3N2Zz4=) 50% 50% no-repeat;
    background-size: contain;
    cursor: pointer;
    opacity: .8
  }

  .suggestions-addon[data-addon-type=clear]:hover {
    opacity: 1
  }

  .suggestions-suggestions {
    background: #fff;
    border: 1px solid #999;
    -ms-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    cursor: default;
    z-index: 9999;
    -webkit-text-size-adjust: 100%
  }

  .suggestions-suggestions strong {
    font-weight: 400;
    color: #39f
  }

  .suggestions-suggestions.suggestions-mobile {
    border-style: none
  }

  .suggestions-suggestions.suggestions-mobile .suggestions-suggestion {
    border-bottom: 1px solid #ddd
  }

  .suggestions-suggestion {
    padding: 4px 4px;
    overflow: hidden
  }

  .suggestions-suggestion:hover {
    background: #f7f7f7
  }

  .suggestions-selected {
    background: #f0f0f0
  }

  .suggestions-selected:hover {
    background: #f0f0f0
  }

  .suggestions-hint {
    padding: 4px 4px;
    white-space: nowrap;
    overflow: hidden;
    color: #777;
    font-size: 85%;
    line-height: 20px
  }

  .suggestions-constraints {
    list-style: none !important;
    margin: 0 !important;
    padding: 0 !important;
    position: absolute !important;
    white-space: nowrap !important
  }

  .suggestions-constraints:empty {
    display: none !important
  }

  .suggestions-constraints li {
    background: #f8f8f8;
    border: 1px solid #ccc;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    cursor: default;
    display: inline-block;
    margin: 0 4px 0 0;
    padding: 0 .5em
  }

  .suggestions-constraints li .suggestions-remove {
    height: 1em;
    width: 1em;
    display: inline-block;
    margin: 0 0 0 .25em;
    background: transparent url(data:image/svg+xml;base64,PHN2ZyB3aWR0aD0nMTAwJyBoZWlnaHQ9JzEwMCcgeG1sbnM9J2h0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnJz48Zz48cGF0aCBkPSdtMjAsMjBtMTAsMGwyMCwyMGwyMCwtMjBsMTAsMTBsLTIwLDIwbDIwLDIwbC0xMCwxMGwtMjAsLTIwbC0yMCwyMGwtMTAsLTEwbDIwLC0yMGwtMjAsLTIwbDEwLC0xMCcgZmlsbD0nIzk5OScvPjwvZz48L3N2Zz4=) 50% 50% no-repeat;
    background-size: contain;
    cursor: pointer;
    opacity: .8
  }

  .suggestions-constraints li .suggestions-remove:hover {
    opacity: 1
  }

  .suggestions-constraints li span {
    vertical-align: middle
  }

  .suggestions-subtext {
    color: #777;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis
  }

  .suggestions-subtext_inline {
    display: inline-block;
    min-width: 6em;
    vertical-align: bottom;
    margin: 0 .5em 0 0
  }

  .suggestions-subtext-delimiter {
    display: inline-block;
    width: 2px
  }

  .suggestions-subtext_label {
    margin: 0 0 0 .25em;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    padding: 0 3px;
    background: #f5f5f5;
    font-size: 85%
  }

  .suggestions-value[data-suggestion-status=LIQUIDATED] {
    position: relative
  }

  .suggestions-value[data-suggestion-status=LIQUIDATED]:after {
    position: absolute;
    left: 0;
    right: 0;
    top: 50%;
    border-top: 1px solid rgba(0, 0, 0, .4);
    content: ""
  }

  .suggestions-promo {
    font-size: 85%;
    display: none;
    color: #777;
    padding: 4px;
    text-align: center
  }

  .suggestions-promo a {
    color: #777;
    display: block;
    filter: grayscale(100%);
    line-height: 20px;
    text-decoration: none
  }

  .suggestions-promo a:hover {
    filter: grayscale(0)
  }

  .suggestions-promo svg {
    height: 20px;
    vertical-align: bottom
  }

  .suggestions-promo-desktop {
    position: absolute;
    top: 0;
    right: 0;
    text-align: left
  }
</style>
